/* Header.css */

.app-Header {
    width: 100%;
    background : white ;
    
    padding: 10px 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    position: fixed ;
    top:  0;
    z-index: 1000;
    justify-content: space-between;
    align-items: center;
  }
  
  .menu-toggle {
    display: none;
    background: none;
    border: none;
    font-size: 1.5em;
    cursor: pointer;
  }
  
  .app-Header .list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .app-Header .list li {
    margin: 0 15px;
  }
  
  .app-Header .list li a {
    color: black;
    text-decoration: none;
    font-size: 1.2em;
    transition: color 0.3s ease;
  }
  
  .app-Header .list li a:hover {
    color: #ff9900;
  }
  
  .app-Header .list .active a {
    color: #ff9900;
  }
  
  @media (max-width: 768px) {
    .menu-toggle {
      display: block;
    }
  
    .app-Header .list {
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      display: none;
      background-color: white;
      position: absolute;
      top: 60px;
      left: 0;
      padding: 10px 0;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      z-index: 1;
    }
  
    .app-Header .list.open {
      display: flex;
    }
  
    .app-Header .list li {
      margin: 10px 0;
      width: 100%;
      text-align: center;
    }
  
    .app-Header .list li a {
      font-size: 1em;
      width: 100%;
      padding: 10px;
    }
  }
  