.controll-data {
    width: 100vw;
    height: 100vh;
    background: url('../img/backgroundimg.jpg');
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

.controll-data > .controll > h1 {
    text-transform: uppercase;
}

.controll-data > .controll {
    z-index: 2;
    width: 40%;
    height: 80%;
    background: transparent;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    align-items: center;
    box-shadow: 0 0 3px black, 0 0 20px rgb(6, 5, 5), 0 0 40px rgb(46, 46, 46);
}

.controll-data > .controll h1 {
    text-shadow: 1px 1px 3px wheat, 1px 1px 6px rgb(245, 245, 245);
}

.controll-data > .controll > div {
    width: 100%;
    height: max-content;
    align-items: center;
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
}

/* Media queries for responsiveness */
@media (max-width: 1200px) {
    .controll-data > .controll {
        width: 50%;
    }
}

@media (max-width: 992px) {
    .controll-data > .controll {
        width: 60%;
    }
}

@media (max-width: 768px) {
    .controll-data > .controll {
        width: 70%;
    }

    .controll-data > .controll > div {
        flex-direction: column;
    }
}

@media (max-width: 576px) {
    .controll-data > .controll {
        width: 90%;
        height: auto;
    }

    .controll-data > .controll > div {
        width: 100%;
    }
}
