.data-room{
    width: 100vw;
    height: 100vh;
    display: flex;
    background: url('../img/backgroundimg.jpg');
    background-size: cover;
    justify-content: center;
    align-items: center;
}
.data-room > .room{

    width: 80%;
    height: 80%;
    background: #28b01e09 ;
     margin-top:  auto;
    margin: 10px auto 0;
    display: flex;
    border-radius: 5px;
    justify-content: space-between;
    box-shadow: 0 0 3px black 
    , 0 0 20px rgb(6, 5, 5) 
    , 0 0 40px rgb(46, 46, 46);
}
.data-room > .room > button{
    height: max-content;
    cursor: pointer;
    border: 2px black solid;
    border-radius: 50%;
    outline: none;
    background: transparent;

    font-size: 1.5rem;
    padding: 10px;
    margin: auto 2px;
}
.data-room > .room > button:hover{
    color :white ;
    background: black;
    
   
}

