/* Room.css */

.single-Room {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  text-transform: capitalize;
  margin: 20px auto;
}

.single-Room > h1 {
  font-size: 2em;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: #333;
}

.room-data {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.room-data > div {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #f6ca03dd;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  margin: 10px;
  text-align: center;
  font-size: 1.2em;
  color: #333;
}

.room-data > div span {
  display: block;
  font-size: 1.4em;
  font-weight: bold;
  color: #000;
}

@media (max-width: 768px) {
  .room-data {
    width: 100%;
  }

  .room-data > div {
    width: 120px;
    height: 120px;
    font-size: 1em;
  }

  .single-Room > h1 {
    font-size: 1.5em;
  }
}

@media (max-width: 480px) {
  .room-data > div {
    width: 100px;
    height: 100px;
    font-size: 0.9em;
  }

  .single-Room > h1 {
    font-size: 1.2em;
  }
}
