.contact {
    width: 100vw;
    height: 100vh;
    background: url('../img/backgroundimg.jpg');
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-div {
    padding: 20px;
    width: 40%;
    max-width: 600px;
    height: auto;
    background: rgba(255, 255, 255, 0.9);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease-in-out;
}

.contact-div:hover {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
}

.contact-div h1 {
    text-transform: uppercase;
    text-shadow: 1px 1px 3px white, 2px 2px 6px rgba(242, 239, 239, 0.935);
    color: #333;
    margin-bottom: 15px;
}

.contact-div p {
    width: 90%;
    text-shadow: 1px 1px 3px white, 2px 2px 6px rgba(242, 239, 239, 0.935);
    text-align: center;
    color: #555;
    margin-bottom: 15px;
}

.contact-div > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 90%;
    padding: 10px;
}

.contact-div > form > :is(input, textarea) {
    width: 100%;
    padding: 10px;
    border: none;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 5px;
    border: 2px solid #ccc;
    outline: none;
    font-size: 1rem;
    transition: border-color 0.3s ease;
}

.contact-div > form > :is(input, textarea):focus {
    border-color: #007BFF;
}

.contact-div > form > :is(input, textarea)::placeholder {
    color: #333;
}

input[type="submit"] {
    width: 100%;
    padding: 10px;
    border: none;
    background: #007BFF;
    color: white;
    text-transform: uppercase;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

textarea {
    width: 100%; 
    height: 100px;
    resize: none;
}

input[type="submit"]:hover {
    background: #0056b3;
}

.contact-div > .contact-form > .error {
    background: white;
    padding: 10px;
    text-transform: uppercase;
    border-radius: 10px;
    color: red;
    width: 90%;
    text-align: center;
    margin-bottom: 10px;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
    .contact-div {
        width: 60%;
        padding: 15px;
    }

    .contact-div p {
        width: 100%;
    }

    .contact-div > form {
        width: 100%;
    }

    .contact-div > form > :is(input, textarea) {
        width: 90%;
    }
}

@media (max-width: 576px) {
    .contact-div {
        width: 80%;
        padding: 10px;
    }

    .contact-div p {
        width: 100%;
    }

    .contact-div > form {
        width: 100%;
    }

    .contact-div > form > :is(input, textarea) {
        width: 80%;
    }

    input[type="submit"] {
        width: 100%;
        color: black !important;
    }
}
