/* Home.css */

.app-Home {
    height: 100vh;
    padding-top: 5px;
    padding-bottom: 5px;
    width: 100%;
    overflow: hidden;
    background: url('../img/backgroundimg.jpg') no-repeat center center fixed;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  .app-Home::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6); /* Subtle dark overlay */
    z-index: 1;
  }
  
  .Home-container {
    position: relative;
    z-index: 2;
    width: 60%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: capitalize;
    margin-top: 50px;
    padding: 20px;
  }
  
  .Home-container > h1 {
    margin: 50px 0;
    width: 90%;
    letter-spacing: 4px;
    line-height: 1.2;
    color: white;
    font-size: 2.5em;
    text-align: center;
    animation: txt 8s ease-in-out infinite;
    text-transform: uppercase;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  }
  
  .Home-container > p {
    font-size: 1.5em;
    width: 90%;
    letter-spacing: 2px;
    margin: 0 auto 50px auto;
    color: white;
    line-height: 1.6;
    text-align: center;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
  }
  
  .Home-container h1 > span {
    font-size: 1.5em;
    text-transform: uppercase;
  }
  
  .Home-container p > span {
    font-size: 1.25em;
    text-transform: uppercase;
  }
  
  @keyframes txt {
    0% {
      text-shadow: 0 0 0 white, 0 0 0 rgb(243, 243, 243), 0 0 0 rgb(225, 224, 224);
    }
    50% {
      text-shadow: 0 0 0 white, 0 0 20px rgb(240, 240, 240), 0 0 40px rgb(246, 244, 244);
    }
    100% {
      text-shadow: 0 0 0 white, 0 0 0 rgb(238, 235, 235), 0 0 0 rgb(238, 235, 235);
    }
  }
  
  @media (max-width: 1024px) {
    .Home-container {
      width: 70%;
    }
  
    .Home-container > h1 {
      font-size: 2.2em;
    }
  
    .Home-container > p {
      font-size: 1.4em;
    }
  }
  
  @media (max-width: 768px) {
    .Home-container {
      width: 80%;
      margin-top: 20px;
    }
  
    .Home-container > h1 {
      font-size: 2em;
      margin: 30px 0;
    }
  
    .Home-container > p {
      font-size: 1.2em;
      margin-bottom: 30px;
    }
  }
  
  @media (max-width: 480px) {
    .Home-container {
      width: 90%;
      padding: 10px;
    }
  
    .Home-container > h1 {
      font-size: 1.5em;
      margin: 20px 0;
    }
  
    .Home-container > p {
      font-size: 1em;
      margin-bottom: 20px;
    }
  }
  