.btn {
    background: #00000064;
    width: 100px;
    height: 50px;
    border-radius: 30px;
    overflow: hidden;
}

.btn>label {
    background: #c4bfbf;
    display: block;
    width: 100%;
    height: 100%;
    position: relative;
    transition: all 1.5s linear;
}
.btn > label.off::after {
    content: '';
    height: 40px;
    top: 5px;
    width: 40px;
    position: absolute;
    border-radius: 50%;
    background: #b70c1a;
    transition: all 1.5s linear;
    left: 5px;
    display: block;

}
.btn > label.on::after {
    content: '';
    height: 40px;
    top: 5px;
    width: 40px;
    position: absolute;
    border-radius: 50%;
    background: #2da027;
    transition: all 1.5s linear;
    right: 5px;
    display: block;
}
